import React from "react";
import htmlAstScraper from "../../htmlAstScraper";
import { Link as GatsbyLink } from "gatsby";

import Drawer from "../../components/Drawer";
import IconButton from "../../components/IconButton";
import Hamburger from "../../../icons/hamburger";

import { useDisclosureWithAnimation } from "../../../hooks/useDisclosure";

import "./styles.scss";

const DesktopNavBar = ({ links }) => {
    return (
        <div className="DesktopNavigation">
            <nav className="NavBar">
                <ul>
                    {links.map((l, idx) => {
                        return (
                            <li key={idx}>
                                <GatsbyLink
                                    color='black.500'
                                    activeStyle={l.isActive ? { color: "#ffb6be", fontWeight: 'bold' } : {}}
                                    to={l.href}>
                                    {l.text}
                                </GatsbyLink>
                            </li>
                        );
                    })}
                </ul>
            </nav>
        </div>
    );
};

const MobileNavBar = ({ links, hamburgerIconColor }) => {
    const {
        state,
        onOpen,
        onClose,
    } = useDisclosureWithAnimation();

    return (
        <nav className="MobileNavBar">
            <IconButton
                colorScheme='white'
                onClick={onOpen}
                ariaLabel='Open Navigation'>
                <Hamburger iconColor={hamburgerIconColor} />
            </IconButton>

            <Drawer
                state={state}
                onClose={onClose}
            >
                <ul className="MobileNavBar">
                    {links.map((l, idx) => {
                        return (
                            <li key={idx}>
                                <GatsbyLink
                                    color='black.500'
                                    activeStyle={l.isActive ? { color: "#ffb6be", fontWeight: 'bold' } : {}}
                                    onClick={() => {
                                        onClose();
                                    }}
                                    to={l.href}>
                                    {l.text}
                                </GatsbyLink>
                            </li>
                        );
                    })}
                </ul>
            </Drawer>
        </nav>
    );
};

export const NavBar = ({ curRoute, hamburgerIconColor = 'black', htmlAst }) => {
    const set = new Set(['a']);
    const links = htmlAstScraper(htmlAst, set).filter(l => {
        return l.children.length > 0 && l.children[0].value;
    }).map(l => {
        const href = l.properties.href,
            normalizedHref = href.charAt(href.length - 1) === '/' ? href.slice(0, href.length - 1) : href,
            normalizedHrefCurRoute = curRoute.charAt(curRoute.length - 1) === '/' ? curRoute.slice(0, curRoute.length - 1) : curRoute;
        return {
            isActive: normalizedHref === normalizedHrefCurRoute,
            text: l.children[0].value,
            href: l.properties.href,
        };
    });


    return (
        <>
            <MobileNavBar links={links} hamburgerIconColor={hamburgerIconColor} />
            <DesktopNavBar links={links} />
        </>
    );

};

export default NavBar;